import { ChevronUp, ChevronDown } from "lucide-react";

type Transaction = {
  id: number;
  customer_name: string;
  amount: number;
  date: string;
  status: string;
};

type Props = {
  data: Transaction[];
  sortBy: string;
  onSort: (field: "amount" | "date") => void;
};

export default function TransactionTable({ data, sortBy, onSort }: Props) {
  const getStatusColor = (status: string) => {
    switch (status.toLowerCase()) {
      case "completed":
        return "bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-400";
      case "pending":
        return "bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400";
      case "failed":
        return "bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400";
      default:
        return "bg-gray-100 text-gray-700 dark:bg-gray-800 dark:text-gray-400";
    }
  };

  return (
    <div className="overflow-hidden rounded-2xl border border-gray-100 bg-white shadow-[0_8px_30px_rgb(0,0,0,0.04)] dark:border-gray-800 dark:bg-gray-900">
      <table className="w-full text-left text-sm">
        <thead className="bg-gray-50/50 text-gray-400 dark:bg-gray-800/30 dark:text-gray-500">
          <tr>
            <th className="px-6 py-5 font-semibold uppercase tracking-wider text-[11px]">Customer</th>
            <th
              className="cursor-pointer px-6 py-5 font-semibold uppercase tracking-wider text-[11px] transition-colors hover:text-blue-600 dark:hover:text-blue-400"
              onClick={() => onSort("amount")}
            >
              <div className="flex items-center gap-1">
                Amount
                {sortBy === "amount" && <ChevronUp className="h-3.5 w-3.5" />}
              </div>
            </th>
            <th
              className="cursor-pointer px-6 py-5 font-semibold uppercase tracking-wider text-[11px] transition-colors hover:text-blue-600 dark:hover:text-blue-400"
              onClick={() => onSort("date")}
            >
              <div className="flex items-center gap-1">
                Date
                {sortBy === "date" && <ChevronUp className="h-3.5 w-3.5" />}
              </div>
            </th>
            <th className="px-6 py-5 font-semibold uppercase tracking-wider text-[11px]">Status</th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-50 dark:divide-gray-800">
          {data.length === 0 ? (
            <tr>
              <td colSpan={4} className="px-6 py-16 text-center text-gray-300">
                No transactions found
              </td>
            </tr>
          ) : (
            data.map((tx) => (
              <tr
                key={tx.id}
                className="group transition-all hover:bg-blue-50/30 dark:hover:bg-blue-900/10"
              >
                <td className="px-6 py-5 font-medium text-gray-700 dark:text-gray-200">
                  {tx.customer_name}
                </td>
                <td className="px-6 py-5 text-gray-600 dark:text-gray-400 font-medium">
                  ${tx.amount.toLocaleString()}
                </td>
                <td className="px-6 py-5 text-gray-400 dark:text-gray-500">
                  {new Date(tx.date).toLocaleDateString(undefined, {
                    year: 'numeric',
                    month: 'short',
                    day: 'numeric'
                  })}
                </td>
                <td className="px-6 py-5">
                  <span
                    className={`inline-flex items-center rounded-lg px-2.5 py-1 text-[11px] font-bold uppercase tracking-wider ${getStatusColor(
                      tx.status
                    )}`}
                  >
                    {tx.status}
                  </span>
                </td>
              </tr>
            ))
          )}
        </tbody>
      </table>
    </div>
  );
}
