"use client";
import { useEffect, useState } from "react";
import TransactionTable from "@/components/TransactionTable";
import Pagination from "@/components/Pagination";
import { Search, Filter, RefreshCw } from "lucide-react";

const API_URL = "http://localhost:3001/transactions";

export default function Home() {
  const [data, setData] = useState<any[]>([]);
  const [page, setPage] = useState(1);
  const [sortBy, setSortBy] = useState("date");
  const [status, setStatus] = useState("All");
  const [search, setSearch] = useState("");
  const [total, setTotal] = useState(0);
  const [loading, setLoading] = useState(false);

  const fetchData = () => {
    setLoading(true);
    const params = new URLSearchParams({
      page: String(page),
      sortBy,
      status,
      search,
    });

    fetch(`${API_URL}?${params.toString()}`)
      .then((res) => res.json())
      .then((result) => {
        setData(result.data);
        setTotal(result.total);
      })
      .catch((err) => console.error(err))
      .finally(() => setLoading(false));
  };

  useEffect(() => {
    fetchData();
  }, [page, sortBy, status, search]); 

  return (
    <main className="min-h-screen bg-white py-12 dark:bg-gray-950 transition-colors duration-300">
      <div className="mx-auto max-w-5xl px-4 sm:px-6 lg:px-8">
        <div className="mb-10 flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
          <div>
            <h1 className="text-3xl font-extrabold tracking-tight text-gray-900 dark:text-gray-100">
              Transactions
            </h1>
            <p className="mt-2 text-sm text-gray-400 dark:text-gray-400 font-medium">
              Manage and track your customer payments with precision.
            </p>
          </div>
          <div className="flex items-center gap-2">
             <button 
                onClick={fetchData} 
                className="inline-flex items-center justify-center rounded-lg border border-gray-100 bg-white p-2.5 text-gray-400 hover:bg-gray-50 hover:text-blue-600 dark:border-gray-800 dark:bg-gray-900 dark:text-gray-400 dark:hover:bg-gray-800 dark:hover:text-blue-400 transition-all shadow-sm"
                title="Refresh"
             >
                <RefreshCw className={`h-4 w-4 ${loading ? 'animate-spin' : ''}`} />
             </button>
          </div>
        </div>

        <div className="mb-8 flex flex-col gap-4 sm:flex-row sm:items-center">
          <div className="relative flex-1 group">
            <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3.5">
              <Search className="h-4 w-4 text-gray-300 group-focus-within:text-blue-500 transition-colors" />
            </div>
            <input
              type="text"
              placeholder="Search by customer name..."
              className="block w-full rounded-xl border border-gray-100 bg-white py-3 pl-11 pr-4 text-sm outline-none focus:border-blue-500/50 focus:ring-4 focus:ring-blue-500/5 dark:border-gray-800 dark:bg-gray-900 dark:text-gray-100 dark:focus:border-blue-500/50 transition-all placeholder:text-gray-300"
              value={search}
              onChange={(e) => {
                setSearch(e.target.value);
                setPage(1); 
              }}
            />
          </div>

          <div className="relative w-full sm:w-56 group">
            <div className="pointer-events-none absolute inset-y-0 left-0 flex items-center pl-3.5">
              <Filter className="h-4 w-4 text-gray-300 group-focus-within:text-blue-500 transition-colors" />
            </div>
            <select
              className="block w-full appearance-none rounded-xl border border-gray-100 bg-white py-3 pl-11 pr-10 text-sm outline-none focus:border-blue-500/50 focus:ring-4 focus:ring-blue-500/5 dark:border-gray-800 dark:bg-gray-900 dark:text-gray-100 dark:focus:border-blue-500/50 transition-all text-gray-600"
              value={status}
              onChange={(e) => {
                setStatus(e.target.value);
                setPage(1); 
              }}
            >
              <option value="All">All Status</option>
              <option value="Completed">Completed</option>
              <option value="Pending">Pending</option>
              <option value="Failed">Failed</option>
            </select>
            <div className="pointer-events-none absolute inset-y-0 right-0 flex items-center pr-3.5 text-gray-300">
               <ChevronDown className="h-4 w-4" />
            </div>
          </div>
        </div>

        <div className="relative">
          {loading && (
            <div className="absolute inset-0 bg-white/50 dark:bg-gray-950/50 z-10 flex items-center justify-center backdrop-blur-[1px] rounded-xl transition-all">
              <RefreshCw className="h-8 w-8 text-blue-500 animate-spin" />
            </div>
          )}
          <TransactionTable data={data} sortBy={sortBy} onSort={setSortBy} />
        </div>
        
        <Pagination
          total={total}
          currentPage={page}
          onPageChange={setPage}
        />
      </div>
    </main>
  );
}
