import { ChevronLeft, ChevronRight } from "lucide-react";

type Props = {
  total: number;
  currentPage: number;
  limit?: number;
  onPageChange: (page: number) => void;
};

export default function Pagination({
  total,
  currentPage,
  limit = 10,
  onPageChange,
}: Props) {
  const totalPages = Math.ceil(total / limit);

  if (totalPages <= 1) return null;

  return (
    <div className="mt-8 flex flex-col sm:flex-row items-center justify-between gap-4 border-t border-gray-50 pt-6 dark:border-gray-800">
      <div className="text-[13px] text-gray-400 dark:text-gray-500 font-medium">
        Showing <span className="text-gray-900 dark:text-gray-200 font-bold">{(currentPage - 1) * limit + 1}</span> to{" "}
        <span className="text-gray-900 dark:text-gray-200 font-bold">
          {Math.min(currentPage * limit, total)}
        </span>{" "}
        of <span className="text-gray-900 dark:text-gray-200 font-bold">{total}</span> results
      </div>
      <div className="flex items-center gap-1.5">
        <button
          disabled={currentPage === 1}
          onClick={() => onPageChange(currentPage - 1)}
          className="inline-flex items-center gap-1.5 rounded-xl border border-gray-100 bg-white px-4 py-2 text-[13px] font-bold text-gray-600 hover:bg-gray-50 hover:text-blue-600 disabled:cursor-not-allowed disabled:opacity-30 dark:border-gray-800 dark:bg-gray-900 dark:text-gray-400 dark:hover:bg-gray-800/50 transition-all shadow-sm"
        >
          <ChevronLeft className="h-4 w-4" />
          Prev
        </button>

        <div className="hidden md:flex items-center gap-1">
          {Array.from({ length: totalPages }).map((_, i) => {
            const page = i + 1;
            if (totalPages > 7 && Math.abs(page - currentPage) > 2 && page !== 1 && page !== totalPages) {
                 if (Math.abs(page - currentPage) === 3) return <span key={page} className="px-1 text-gray-300">...</span>;
                 return null;
            }
            
            return (
              <button
                key={page}
                onClick={() => onPageChange(page)}
                className={`inline-flex h-9 w-9 items-center justify-center rounded-xl text-[13px] font-bold transition-all ${
                  currentPage === page
                    ? "bg-blue-600 text-white shadow-md shadow-blue-200 dark:shadow-none"
                    : "text-gray-400 hover:bg-gray-50 hover:text-blue-600 dark:text-gray-500 dark:hover:bg-gray-800"
                }`}
              >
                {page}
              </button>
            );
          })}
        </div>

        <button
          disabled={currentPage === totalPages}
          onClick={() => onPageChange(currentPage + 1)}
          className="inline-flex items-center gap-1.5 rounded-xl border border-gray-100 bg-white px-4 py-2 text-[13px] font-bold text-gray-600 hover:bg-gray-50 hover:text-blue-600 disabled:cursor-not-allowed disabled:opacity-30 dark:border-gray-800 dark:bg-gray-900 dark:text-gray-400 dark:hover:bg-gray-800/50 transition-all shadow-sm"
        >
          Next
          <ChevronRight className="h-4 w-4" />
        </button>
      </div>
    </div>
  );
}
